  -webkit-animation: enter 2s ease-in-out both;
          animation: enter 2s ease-in-out both;

}
.iconHeader :hover {
  color: var(--main-color)
}

.icon :hover {
  color: var(--main-color)
}


.icon {
 padding: 5px;
color: var(--dark-main-color);
}

header img {
  border-radius: 50%;
  width: 150px;
  border: 1px solid;
    -webkit-animation: enter 2s ease-in-out both;
            animation: enter 2s ease-in-out both;}
header {
  position: relative;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: flex-end;
  align-content: center;
  flex-wrap: wrap;
  padding: 2em;
  background: var(--light-color);
  background: -webkit-gradient(
    linear,
   left bottom, left top,
   color-stop(44%, var(--light-color)),
to(rgba(148, 11, 153, 0.35)) );
  background: linear-gradient(
    0deg,
   var(--light-color) 44%,
rgba(148, 11, 153, 0.35) 100% );
}

#scrollDownButton a {
  padding-top: 60px;
  -webkit-animation:pop-up 3s  forwards;
          animation:pop-up 3s  forwards;

}
#scrollDownButton a span {
  position: absolute;
  left: 50%;
  width: 46px;
  height: 46px;
  margin-left: -23px;
  border: 1px solid var(--main-color);
  border-radius: 100%;
  box-sizing: border-box;
}
#scrollDownButton a span::after {
  position: absolute;
  top: 50%;
  left: 50%;
  content: '';
  width: 16px;
  height: 16px;
  margin: -12px 0 0 -8px;
  border-left: 1px solid var(--main-color);
  border-bottom: 1px solid var(--main-color);
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  box-sizing: border-box;
}
.socialMedia {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 5px;;
}

@-webkit-keyframes pop-up {
  0% {
    -webkit-transform: scale(0) translateY(-10px);
            transform: scale(0) translateY(-10px);
    opacity: 0;
  }

  80% {
    -webkit-transform: scale(1.2) translateY(-2px);
            transform: scale(1.2) translateY(-2px);
    opacity: 0.8;
  }

  100% {
    -webkit-transform: scale(1) translateY(0px);
            transform: scale(1) translateY(0px);
    opacity: 1;
  }
}

@keyframes pop-up {
  0% {
    -webkit-transform: scale(0) translateY(-10px);
            transform: scale(0) translateY(-10px);
    opacity: 0;
  }

  80% {
    -webkit-transform: scale(1.2) translateY(-2px);
            transform: scale(1.2) translateY(-2px);
    opacity: 0.8;
  }

  100% {
    -webkit-transform: scale(1) translateY(0px);
            transform: scale(1) translateY(0px);
    opacity: 1;
  }
}
@-webkit-keyframes enter {
  from {
    opacity: 1;
    -webkit-transform: scale(0);
            transform: scale(0)
  }

  to {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1)
  }
}
@keyframes enter {
  from {
    opacity: 1;
    -webkit-transform: scale(0);
            transform: scale(0)
  }

  to {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1)
  }
}

.aboutSection,
.contactSection
{
  padding: 20px 0;
  min-height: 100%;
