  }
}
.contactForm {
  display: flex;
  /* flex-direction: column; */
  justify-content: space-between;
  /* align-items: center; */
  width: 100%;
  max-width: 600px;
  min-height: 400px;
  margin: 0 auto;
  padding: 20px 0;
}

.contactForm label {
  display: block;
  padding: 20px 0 5px;
  width: 66%;
  font-size: .8rem;
  color: var(--dark-main-color)
}


.name,
.email,
.message {
  display: block;
  padding: 5px 10px 5px;
  min-width: 66%;
  max-width: 66%;
  border: 2px solid transparent;
  border-bottom: 2px solid var(--main-color);
  background: none;
  font-size: 1rem;
  font-family: "Comfortaa", sans-serif;

}
.message {
  border-color: var(--main-color);
}

.name:focus,
.email:focus,
.message:focus {
  background: var(--light-main-color);
  outline: none;
}

.error {
  color: rgb(199, 0, 0);
  font-size: 10px;
}

.formButton {
  margin: 30px 0;
  padding: 10px 30px;
  border: 3px solid var(--main-color);
  color: var(--dark-main-color);
  background: none;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 1px;
  -webkit-transition: .3s;
  transition: .3s;
  cursor: pointer;
}

.formButton:hover {
  background: var(--main-color);
}

.red,
